<template>
	<div>
		<p class="title">
			{{list.num}} {{list.title}}
		</p>
		<div class="content" v-html="list.knowledge">
		
		</div>
		<div class="btn" @click="go(list.id)">交流区</div>
		<Bottom></Bottom>
	</div>
</template>

<script>

	import Bottom from '../../components/Bottom/Bottom'
	
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				     id:this.$route.params.ConsoledateId,
				     list:{}
			} 
		},
		mounted(){
			this.getData()
		
			
		},
		methods:{
			go(id){
				this.$router.push({name: 'Communication',params:{
					ConsoledateId:id
				}})
			},
			getData(){
				var obj={
					"s":this.$store.state.sessionId,
					"id":this.id
				}
				
				this.$Api.MinutiaDetails(obj).then((res)=>{
					
					this.list=res.q.minutia
				})
				
				
			}
			
			
		}
		
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	.title{
		padding: 0.3rem;
		font-size: 0.32rem;
		
	}
	.content{
		padding: 0.3rem;
		height: 7.5rem;
		overflow-y: scroll;
		font-size: 0.3rem;
		margin-bottom: 0.6rem;
	}
	.btn{
		width:6.9rem;
		height: 0.9rem;
		border-radius:0.9rem ;
		background: #fa8a12;
		text-align: center;
		line-height: 0.9rem;
		color: #FFFFFF;
		font-size: 0.36rem;
		margin:0.3rem auto;
	}
</style>


